:root {
  --brand-color: #61dafb;
}

.root {
  background: #262626;
  color: #29a9dc;
  width: 100%;
  opacity: 0.8;
}

.container {
  margin: 0 auto;
  padding: 20px 0;
  max-width: var(--max-content-width);
}

.brand {
  color: color(var(--brand-color) lightness(+10%));
  text-decoration: none;
  font-size: 1.75em;
}

.banner {
  text-align: center;
}

.bannerTitle {
  margin: 0;
  padding: 10px;
  font-weight: normal;
  font-size: 4em;
  line-height: 1em;
}

.bannerDesc {
  padding: 0;
  color: #fff;
  font-size: 1.25em;
  margin: 0;
}
