:root {
  --brand-color: #61dafb;
}

.root {
  background: #262626;
  color: #29a9dc;
  width: 100%;
  opacity: 0.8;
}

.container {
  margin: 0 auto;
  padding: 20px 0;
  max-width: var(--max-content-width);
}

.brand {
  color: color(#61dafb lightness(+10%));
  color: color(var(--brand-color) lightness(+10%));
  text-decoration: none;
  font-size: 1.75em;
}

.banner {
  text-align: center;
}

.bannerTitle {
  margin: 0;
  padding: 10px;
  font-weight: normal;
  font-size: 4em;
  line-height: 1em;
}

.bannerDesc {
  padding: 0;
  color: #fff;
  font-size: 1.25em;
  margin: 0;
}

.footer {
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  background: #262626;
  color: #fff;
  opacity: 0.8;
}

.container {
  margin: 0 auto;
  padding: 20px 15px;
  max-width: var(--max-content-width);
  text-align: center;
}

.text {
  color: #fff;
}

html {
  font-size: 17px;
}

body {
  font-family: "Nunito", sans-serif;
  background-image: url(/loan-calc-2019/static/media/background.24e62cb4.png);
  margin: 0;
}

p {
  font-size: 2rem;
  margin: 0;
}

small {
  font-weight: lighter;
  color: gray;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  text-align: center;
  padding: 2rem;
  border-radius: 0.5rem;
  width: 12rem;
  margin: 10vh auto;
  box-shadow: 15px 20px 70px -10px;
  background: white;
}

.input-range__label--max {
  display: none;
}

.input-range__label--min {
  display: none;
}

.input-range__label-container {
  left: -50%;
  position: relative;
  display: none;
}

